<template>
  <div>
      <detail-banner :sightName="sightName" :bannerImg="bannerImg" :bannerImgs="gallaryImgs"></detail-banner>
      <detail-header></detail-header>
      <div class="content">
        <detail-list :list="list"></detail-list>
      </div>
  </div>
</template>

<script>
import DetailBanner from './components/Banner'
import DetailHeader from './components/Header'
import DetailList from './components/List'
import axios from 'axios'

export default {
  name: 'Detail',
  components: {
    DetailBanner,
    DetailHeader,
    DetailList
  },
  data () {
    return {
      sightName: '',
      bannerImg: '',
      gallaryImgs: [],
      list: [{
        title: '成人票1',
        children: [{
          title: '成人票1AAAAAAAAA',
          children: [{
            title: '成人票1AAAChildren'
          }]
        },
        {
          title: '成人票1BBBBBBBBB'
        }]
      },
      {
        title: '成人票2'
      },
      {
        title: '成人票3'
      },
      {
        title: '成人票4'
      }]
    }
  },
  methods: {
    getDetailInfo () {
      axios.get('/static/mock/index.json', {
        params: {
          id: this.$route.params.id
        }
      }).then(this.handleGetDataSucc)
    },
    handleGetDataSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.sightName = data.sightName
        // this.bannerImg = data.bannerImg
        // this.gallaryImgs = data.gallaryImgs
        this.sightName = '旅游景点'
        this.bannerImg = 'http://img1.qunarzz.com/sight/p0/1409/19/adca619faaab0898245dc4ec482b5722.jpg_600x330_f922b488.jpg'
        this.gallaryImgs = ['http://img1.qunarzz.com/sight/p0/1409/19/adca619faaab0898245dc4ec482b5722.jpg_600x330_f922b488.jpg', 'http://img1.qunarzz.com/sight/p0/2005/39/3979f1867defec4ea3.water.jpg_600x330_2106404f.jpg', 'http://img1.qunarzz.com/sight/p0/1602/92/920e47352552c1c990.water.jpg_600x330_ca603c01.jpg']
        // this.list = data.categoryList
        this.list = [{
          title: '成人票1',
          children: [{
            title: '成人票1AAAAAAAAA',
            children: [{
              title: '成人票1AAAChildren'
            }]
          },
          {
            title: '成人票1BBBBBBBBB'
          }]
        },
        {
          title: '成人票2'
        },
        {
          title: '成人票3'
        },
        {
          title: '成人票4'
        }]
      }
    }
  },
  mounted () {
    this.getDetailInfo()
  }
}
</script>

<style lang="stylus" scoped>
.content
  height 50rem
</style>



